import React from 'react';
import {Col, Row} from 'react-bootstrap';
import Clock from './Clock'

export default (props) => (
    <Row>
        <Col xs={12}>
            <h2>
                Domov môžeš vypadnúť{' '}
                <strong>{props.leaveTime}</strong>{' '}
                (o <Clock leavingTime={props.leaveCountDown}/>)
            </h2>
        </Col>
    </Row>
)



