import React, {Component} from 'react';
import * as moment from 'moment';

class Clock extends Component {
    componentWillMount() {
        this.setRemainingTime(this.props.leavingTime);
    }

    componentDidMount() {
        setInterval(() => this.setRemainingTime(this.props.leavingTime), 1000)
    }

    setRemainingTime(leavingTime) {
        const remaining = moment.duration(leavingTime.diff(moment()));
        const hours = remaining.hours();
        const minutes = Clock.addPrefixIfNeeded(remaining.minutes());
        const seconds = Clock.addPrefixIfNeeded(remaining.seconds());
        this.setState({hours, minutes, seconds})
    }

    static addPrefixIfNeeded(number) {
        return number.toLocaleString("sk", {minimumIntegerDigits: 2});
    }

    render() {
        return (
                <span>{this.state.hours}:{this.state.minutes}:{this.state.seconds}</span>
        );
    }
}

export default Clock;


