import React, {Component} from 'react';
import * as moment from 'moment';
import {Col, Grid, Jumbotron, Row} from 'react-bootstrap';
import './style.css';
import Formular from './Formular';
import InfoLine from './InfoLine';

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            timeInWork: '0:0',
            sadlo: '0:0',
            obed: false,
            leavingTime: moment().add(8, 'hours')
        }
    }

    setLeavingTime() {
        let splittedTimeInWork = App.splitToHoursAndMinutes(this.state.timeInWork);
        let splittedSadlo = App.splitToHoursAndMinutes(this.state.sadlo);
        let leavingTime = moment()
            .add(8, 'hours')
            .subtract(splittedSadlo[0], 'hours')
            .subtract(splittedSadlo[1], 'minutes')
            .add(this.state.obed ? 45 : 0, 'minutes')
            .subtract(splittedTimeInWork[0], 'hours')
            .subtract(splittedTimeInWork[1], 'minutes');
        this.setState({leavingTime})
    }

    render() {
        return (
            <Grid>
                <Jumbotron>
                    <Row>
                        <Col xs={12}>
                            <h1>
                                Už sa mi tu nechce byť <span role="img" aria-label="sad">😢</span>
                            </h1>
                        </Col>
                    </Row>
                    <Formular onTimeInput={event => this.setState({timeInWork: event.target.value})}
                              onSadloInput={event => this.setState({sadlo: event.target.value})}
                              onObedClick={event => this.setState({obed: event.target.checked})}
                              onButtonClick={() => this.setLeavingTime()}
                    />
                    <InfoLine leaveTime={this.state.leavingTime.format('HH:mm')}
                              leaveCountDown={this.state.leavingTime}/>
                </Jumbotron>
            </Grid>
        );
    }

    static splitToHoursAndMinutes(time) {
        let splittedTime = time.toString().replace(/(\s+)/, '').split(':');
        if (splittedTime.length > 1 && splittedTime[0].startsWith('-')) {
            splittedTime[1] = splittedTime[1] * -1;
        }
        return splittedTime;
    }
}

export default App;


