import React from 'react';
import {
    Button,
    Checkbox,
    Col,
    ControlLabel,
    Form,
    FormControl,
    FormGroup,
    Row
} from 'react-bootstrap';

const handleKeyPress = (target, action) => {
    if (target.charCode === 13) {
        action();
        target.nativeEvent.preventDefault();
    }
};

export default (props) => (
    <Row>
        <Col xs={12}>
            <Form horizontal>
                <FormGroup>
                    <Col componentClass={ControlLabel} htmlFor={"cas"} md={4} sm={6}>
                        Ako dlho už si v práci? (opíš z <a href="https://app.flowii.com">flowii</a>)
                    </Col>{' '}
                    <Col md={8} sm={6}>
                        <FormControl type="text" placeholder="HH:MM"
                                     onKeyPress={(target) => handleKeyPress(target, props.onButtonClick)}
                                     onChange={props.onTimeInput} id={"cas"} autoComplete="off"
                                     tabIndex={1}/>
                    </Col>
                </FormGroup>

                <FormGroup>
                    <Col componentClass={ControlLabel} htmlFor={"sadlo"} md={4} sm={6}>
                        Aký máš nadčas/saldo? (opíš z <a href="https://app.flowii.com">flowii</a>)
                    </Col>{' '}
                    <Col md={8} sm={6}>
                        <FormControl type="text" placeholder="HH:MM"
                                     onKeyPress={(target) => handleKeyPress(target, props.onButtonClick)}
                                     onChange={props.onSadloInput} id={"sadlo"} autoComplete="off"
                                     tabIndex={2}/>
                    </Col>
                </FormGroup>

                <FormGroup>
                    <Col mdOffset={4} smOffset={6} md={8} sm={6}>
                        <Checkbox onChange={props.onObedClick} tabIndex={3}
                                  onKeyPress={(target) => handleKeyPress(target, props.onButtonClick)}>
                            Ešte ma čaká obed (pričíta sa 45 minút)
                        </Checkbox>
                    </Col>
                </FormGroup>

                <FormGroup>
                    <Col mdOffset={4} smOffset={6} md={8} sm={6}>
                        <Button bsStyle={"primary"} tabIndex={4}
                                onClick={props.onButtonClick}>Potvrď</Button>
                    </Col>
                </FormGroup>

            </Form>
        </Col>
    </Row>
)



